body {
  background: #edd7cf;
}
body * {
  box-sizing: border-box;
}

#api_request {
  /* width: 422px;
  height: 197px; */
  width: 100%;
  height: 12em;
  overflow-y: scroll;
  outline: 2px solid #d5b1a3;
  border-radius: 0.2em;
}

#divRuby {
  height:60px;
  overflow-y:auto;
}

#container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  width: 100%;
  margin: 8vh auto;
}

.inputText {
  padding: 1em;
  max-width: 50%;
  width: 100%;
}
.outputText {
  overflow-y: scroll;
  height: 18em;
  max-width: 50%;
  width: 100%;
  background: #f5e5e0;
  border-radius: .5em;
  padding: 1em;
}
h2 {
  margin: 0;
}
input[type="button"] {
  outline: none;
  border: none;
  background: #d5b1a3;
  padding: 0.3em 0.8em;
  border-radius: 0.5em;
  color: #fff;
  font-weight: 600;
  margin: .5em 0;
}

/* 手機 */
@media (max-width: 768px) {
  .inputText, .outputText {
    max-width: 100%;
  }
}